import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { DRUG_BASE_URL, DrugModel } from '../../../models/drug.model';
import { InAppBrowser, InAppBrowserOptions } from '@ionic-native/in-app-browser';

/**
 * Generated class for the DrugDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'DrugDetailPage'
})
@Component({
  selector: 'page-drug-detail',
  templateUrl: 'drug-detail.html',
})
export class DrugDetailPage {

  public drug: DrugModel;

  public options: InAppBrowserOptions = {
    location: 'yes',
    hidden: 'no',
    clearcache: 'yes',
    clearsessioncache: 'yes',
    zoom: 'yes',
    hardwareback: 'yes',
    mediaPlaybackRequiresUserAction: 'no',
    shouldPauseOnSuspend: 'no',
    closebuttoncaption: 'Close',
    disallowoverscroll: 'no',
    toolbar: 'yes',
    enableViewportScale: 'no',
    allowInlineMediaPlayback: 'no',
    presentationstyle: 'pagesheet',
    fullscreen: 'yes'
  };


  constructor(public navCtrl: NavController, public navParams: NavParams,
              private iab: InAppBrowser, public viewCtrl: ViewController) {
    this.drug = this.navParams.get('drug');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DrugDetailPage');
  }

  openInAppBrowser($event){
    $event.preventDefault();
    this.iab.create(DRUG_BASE_URL + this.drug.mapper.link, '_system', this.options);
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }
}



